.front .hp-galleries {
  @include zen-grid-container;

  background: color(secondary);
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 30px;

  .view-homepage-galleries {
    max-width: 570px;
    margin: 0 auto;

    @include zen-respond-to(xxxl) {
      max-width: 100%;
    }

    .bx-wrapper .bx-viewport {
      background: color(primary);
      -webkit-box-shadow: none;
      box-shadow: none;
    }

    .bx-wrapper .bx-pager {
      bottom: -45px;
    }

    .bx-wrapper .bx-next {
      right: 20px;
    }

    ul {margin: 0;}

  }
}
