.footer-bg {
  display: inline-block;
  width: 100%;
  margin-top: 60px;
  background-color: color(secondary);
  padding: 20px;

  @include zen-respond-to(xs) {
    //text-align: left;
      padding: 20px 0;
  }

  span {
    //font-family: Verdana, Tahoma, "DejaVu Sans", sans-serif;
    font-family: 'Roboto Condensed', sans-serif;
  }

  ul {
    padding: 0;

    @include zen-respond-to(xs) {
      padding: 0 0 0 20px;
      margin: 15px 0;
    }

    @include zen-respond-to(l) {
      margin: 0;
    }

    li {
    list-style: none;
    margin: 0 0 8px;
    border-bottom: 1px solid color(link);

      @include zen-respond-to(xs) {
        border-bottom: 0;
        margin: 0 0 3px;
      }

    }

  }

  p {
  margin: 0 0 8px;
  border-bottom: 1px solid color(link);

    @include zen-respond-to(xs) {
      border-bottom: 0;
      margin: 0 0 3px;
    }

  }

  a {
    color: #e38d61;
    text-decoration: none;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    width: 100%;

    &:visited {
      color: #e38d61;
    }

    &:hover,
    &.active {
      color: color(orange);
    }

  }

  .footer-one {

    @include zen-respond-to(xs) {
      @include zen-grid-item(6, 1);
      min-height: 200px;
      border-left: solid 1px color(primary);
      border-right: solid 1px color(primary);
      border-bottom: solid 1px color(primary);
      padding-left: 20px;
    }

    @include zen-respond-to(l) {
      @include zen-grid-item(3, 1);
      border-bottom: 0;
    }

  }

  .footer-two {

    @include zen-respond-to(xs) {
      @include zen-grid-item(6, 7);
      min-height: 200px;
      border-right: solid 1px color(primary);
      border-bottom: solid 1px color(primary);
      padding-left: 20px;
    }

    @include zen-respond-to(l) {
      @include zen-grid-item(3, 4);
      border-bottom: 0;
    }
    
  }

  .footer-three {

    @include zen-respond-to(xs) {
      @include zen-grid-item(6, 1);
      @include zen-new-row();
      min-height: 200px;
      border-left: solid 1px color(primary);
      border-right: solid 1px color(primary);
    }

    @include zen-respond-to(l) {
      @include zen-grid-item(3, 7);
      @include zen-new-row(none);
    }

  }

  .footer-four {

    @include zen-respond-to(xs) {
      @include zen-grid-item(6, 7);
      min-height: 200px;
      border-right: solid 1px color(primary);
    }

    @include zen-respond-to(l) {
      @include zen-grid-item(3, 10);
    }

  }
}
